import { Component } from '@angular/core';
import { NavController, NavParams, ViewController, AlertController, ToastController } from 'ionic-angular';
import { Validators, FormBuilder, FormGroup } from '@angular/forms';
import { NativePageTransitions, NativeTransitionOptions } from '@ionic-native/native-page-transitions';


@Component({
  selector: 'page-loancalc',
  templateUrl: 'loancalc.html',
})
export class LoancalcPage {
	private loanTypes:any[]=[]
	private loancalcformgroup: FormGroup

  constructor(
  	public navCtrl: NavController, 
  	public navParams: NavParams, 
  	public viewCtrl: ViewController,
  	private alertCtrl:AlertController,
  	private formBuilder: FormBuilder,
  	private nativePageTransitions: NativePageTransitions){
		this.loancalcformgroup = this.formBuilder.group({
			lc_loantype:["",Validators.required],
			lc_amount:["",Validators.required],
			lc_months:["",Validators.required],
			lc_interestrate:["",Validators.required]
		})		  
	}

	ionViewDidLoad(){
		//Loan TYpes
		this.loanTypes = [
			{
				name:"Development Loan",
				value:"development_loan",
				interest_rate:1.23			
			},
			{
				name:"Micro Loan",
				value:"micro_loan",
				interest_rate:1.23				
			},
			{
				name:"M-Loan",
				value:"m_loan",
				interest_rate:1.23				
			},	
			{
				name:"Education Loan",
				value:"education_loan",
				interest_rate:1.23				
			},
			{
				name:"Super Loan",
				value:"super_loan",
				interest_rate:1.23				
			},
			{
				name:"Express Loan",
				value:"express_loan",
				interest_rate:1.23				
			},
			{
				name:"Smart Life Loan",
				value:"smart_life_loan",
				interest_rate:1.23				
			},
			{
				name:"Clear & Apply Loan",
				value:"clear_and_apply_loan",
				interest_rate:1.23				
			},																				
		]
	}

	//handle events
	dismiss(){
		/**Animate*/
	    let options: NativeTransitionOptions = {
	      direction: 'up',
	      duration: 400
	     };
	    this.nativePageTransitions.flip(options);	
	    /**End Animation*/	
		this.viewCtrl.dismiss()
	}  

}



