import { Component } from '@angular/core';
import { NavController, NavParams } from 'ionic-angular';
import { Validators, FormBuilder, FormGroup } from '@angular/forms';
import { HomePage } from '../home/home';

import { SocketHelperProvider } from "../../providers/sockethelper/sockethelper";

@Component({
  selector: 'page-joinapp',
  templateUrl: 'joinapp.html',
})
export class JoinAppPage {
  private joinappformgroup:any
  private phonenumber:any;
  private id:any;
  private membernumber:any;

	constructor(
    public navCtrl: NavController,
    public navParams: NavParams,
    private formBuilder: FormBuilder,
    public socketHelper: SocketHelperProvider) {
      this.joinappformgroup = this.formBuilder.group({
				ja_phonenumber:["",Validators.required],
				ja_id:["",Validators.required],
				ja_membernumber:["",Validators.required]
			})
	}

	ionViewDidLoad() {

	}

	//event handling
	onCancel(){
		this.navCtrl.pop()
	}

	onSubmit(){    
    // let data = {
    //   phonenumber:this.joinappformgroup.value.ja_phonenumber,
    //   membernumber:this.joinappformgroup.value.ja_membernumber,
    //   id:this.joinappformgroup.value.ja_id
    // }
    let data = "testtest"
    
    this.socketHelper
    .joinApp(data)
    .then(()=>{
      this.socketHelper.onVerified()
    })
	}

}



