import { Component } from '@angular/core';
import { NavController, NavParams, ViewController, AlertController } from 'ionic-angular';
import { Validators, FormBuilder, FormGroup } from '@angular/forms';
import { NativePageTransitions, NativeTransitionOptions } from '@ionic-native/native-page-transitions';
import { SocketHelperProvider } from "../../providers/sockethelper/sockethelper";

@Component({
  selector: 'page-fosaadvancemodal',
  templateUrl: 'fosaadvancemodal.html',
})
export class FosaadvancemodalPage {
	private fosaformgroup: FormGroup

	constructor(
		private navCtrl: NavController,
		private navParams: NavParams,
		private viewCtrl: ViewController,
		private alertCtrl: AlertController,
    private formBuilder: FormBuilder,
    private socketHelper: SocketHelperProvider,
		private nativePageTransitions: NativePageTransitions){

		//fosa advance data
		this.fosaformgroup = this.formBuilder.group({
			f_amount:["",Validators.required]
		})
	}

	ionViewDidLoad(){}

	/*handle events*/
	dismiss(){
		/**Animate*/
	    let options: NativeTransitionOptions = {
	      direction: 'up',
	      duration: 400
	     };
	    this.nativePageTransitions.flip(options);
	    /**End Animation*/
		this.viewCtrl.dismiss()
	}

	/*On submit fosa advance function*/
	onSubmitFosa(){
		console.log(this.fosaformgroup.value)

		let payAlert = this.alertCtrl.create({
			title:"Confirm",
			message:`You are about to apply for a Ksh ${this.fosaformgroup.value.f_amount} fosa advance.
					Interest of 5% will be charged. Payable in 30 days`,
			buttons: [
				{
				  text: 'Cancel',
				  handler: () => {
				    console.log('Cancel clicked');
				  }
				},
				{
				  text: 'Agree',
				  handler: () => {
            this.socketHelper.applyFosaAdvance({
              phone_number:254727677068,
              member_number:123456789,
              amount:this.fosaformgroup.value.f_amount
            })
				    console.log('Agree clicked');
				  }
				}
			],
			cssClass:'alertCustomCss'
		})

		payAlert.present()
	}

}



