import { Component } from '@angular/core';
import { NavController, NavParams, ViewController, ToastController, AlertController } from 'ionic-angular';
import { Validators, FormBuilder, FormGroup } from '@angular/forms';
import { NativePageTransitions, NativeTransitionOptions } from '@ionic-native/native-page-transitions';

@Component({
  selector: 'page-buyminutes',
  templateUrl: 'buyminutes.html',
})
export class BuyminutesPage {
	private accounts: any[]=[]
	private airtimeTransaction: FormGroup

	constructor(
		public navCtrl: NavController, 
		public navParams: NavParams,
		public viewCtrl: ViewController,
		public toastCtrl:ToastController,
		private alertCtrl: AlertController,
		private formBuilder: FormBuilder,		
		private nativePageTransitions: NativePageTransitions){

		this.airtimeTransaction = this.formBuilder.group({
			amount:['',Validators.required],
			accountnumber:['',Validators.required]
		})		
	}

	ionViewDidLoad(){
		this.accounts = [
			{				
				name:"Dennis Obel",
				accountnumber:"123456789"				
			},
			{				
				name:"Shem Ogembo",
				accountnumber:"987654321"				
			}		
		]		
	}


	//handle events
	dismiss(){
		/**Animate*/
	    let options: NativeTransitionOptions = {
	      direction: 'up',
	      duration: 400
	     };
	    this.nativePageTransitions.flip(options);	
	    /**End Animation*/	
		this.viewCtrl.dismiss()
	}

	onSubmit(){
		console.log(this.airtimeTransaction.value)


		let payAlert = this.alertCtrl.create({
			title:"Confirm",
			message:`You are about to buy Ksh ${this.airtimeTransaction.value.amount} worth of airtime from account ${this.airtimeTransaction.value.accountnumber}.`,
			buttons: [
				{
				  text: 'Disagree',
				  handler: () => {
				    console.log('Disagree clicked');
				  }
				},
				{
				  text: 'Agree',
				  handler: () => {
				    console.log('Agree clicked');
				  }
				}
			],
			cssClass:'alertCustomCss'			
		})

		payAlert.present()			
	}	

}



