import { Component } from '@angular/core';
import { NavController, NavParams, ViewController, AlertController } from 'ionic-angular';
import { NativePageTransitions, NativeTransitionOptions } from '@ionic-native/native-page-transitions';
import { SocketHelperProvider } from "../../providers/sockethelper/sockethelper";

@Component({
  selector: 'page-guarantees',
  templateUrl: 'guarantees.html',
})
export class GuaranteesPage {
	private grntbtns:string="Guarantees"
	private guarantees:any[]=[]
	private guarantors:any[]=[]

	constructor(
		private navCtrl: NavController,
		private navParams: NavParams,
		private viewCtrl: ViewController,
		private alertCtrl: AlertController,
		private socketHelper: SocketHelperProvider,
		private nativePageTransitions: NativePageTransitions){}

	ionViewDidLoad(){
    this.socketHelper
    .guaranteesGuarantors()
    .then((res)=>{

    })
		this.guarantees=[
			{
				name:"James Kibwana",
				loantype:"FOSA Advance",
				amount:"120000",
				balance:"50000",
				maturity:"12/12/2018"
			},
			{
				name:"Erick Sermon",
				loantype:"Express Loan",
				amount:"120000",
				balance:"50000",
				maturity:"12/12/2018"
			}
		]

		this.guarantors=[
			{
				name:"Tony Were",
				loantype:"FOSA Advance",
				amount:"120000",
			},
			{
				name:"Vincent Juma",
				loantype:"Express Loan",
				amount:"170000",
			}
		]

		console.log(this.guarantees)
	}

	//handle events
	dismiss(){
		/**Animate*/
	    let options: NativeTransitionOptions = {
	      direction: 'up',
	      duration: 400
	     };
	    this.nativePageTransitions.flip(options);
	    /**End Animation*/
		this.viewCtrl.dismiss()
	}

	onMail(){
		let mailAlert = this.alertCtrl.create({
			title:"Mail Guarantors/Guarantees",
			message:"Sending list of guarantors/guarantees to your email will cost you 50/=",
			buttons:[
				{
					text:"Cancel",
					role:"cancel",
					handler:()=>{
						console.log("cancelled")
					}
				},
				{
					text:"Agree",
					handler:()=>{

					}
				}
			],
			cssClass:"alertCustomCss"
		})

		mailAlert.present()
	}

}



