import { Component } from '@angular/core';
import { NavController, ViewController, NavParams, ModalController } from 'ionic-angular';
import { AlertsPage } from '../alerts/alerts';
import { ChatPage } from '../chat/chat';
import { Validators, FormBuilder, FormGroup } from '@angular/forms';
import { NativePageTransitions, NativeTransitionOptions } from '@ionic-native/native-page-transitions';
import { FabtoggleProvider } from "../../providers/fabtoggle/fabtoggle";
import { SocketHelperProvider } from "../../providers/sockethelper/sockethelper";

@Component({
  selector: 'page-balanceenquiry',
  templateUrl: 'balanceenquiry.html',
})
export class BalanceenquiryPage {
	private balanceenquiryformgroup: FormGroup
	private user_accounts:any[]=[]
	private actual_balance: any;
	private available_balance: any;

	constructor(
		private navCtrl: NavController,
		private modalCtrl: ModalController,
		private viewCtrl: ViewController,
		private navParams: NavParams,
		private formBuilder: FormBuilder,
    private fabToggleProvider: FabtoggleProvider,
    private socketHelper: SocketHelperProvider,
    private nativePageTransitions: NativePageTransitions){

			this.balanceenquiryformgroup = this.formBuilder.group({
				benq_account:["",Validators.required]
			})
		}

    ionViewDidLoad(){
		this.user_accounts = [
			{
				account_number:12345678901,
				available_balance:40500,
				actual_balance:40000
			},
			{
				account_number:10987654321,
				available_balance:50500,
				actual_balance:50000
			},
			{
				account_number:98712365410,
				available_balance:60500,
				actual_balance:60000
			}
		]
	}

	dismiss(){
		/**Animate*/
	    let options: NativeTransitionOptions = {
	      direction: 'up',
	      duration: 400
	     };
	    this.nativePageTransitions.flip(options);
	    /**End Animation*/
		this.viewCtrl.dismiss()
	}

	onSubmit(){
    let user = {
      phoneNumber:254727677068,
      memberNumber:123456789
    }

    this.socketHelper.balanceEnq({
      ...user,
      ...this.balanceenquiryformgroup.value
    })
		console.log(this.balanceenquiryformgroup.value.benq_account)
		let filteredBalEnq = this.user_accounts.filter((data)=>{
			return data.account_number === this.balanceenquiryformgroup.value.benq_account
		})

		console.log(filteredBalEnq)
		this.available_balance = filteredBalEnq[0].available_balance
		this.actual_balance = filteredBalEnq[0].actual_balance
	}
}



