import { Component } from '@angular/core';
import { NavController, NavParams, ViewController, AlertController } from 'ionic-angular';
import { Validators, FormBuilder, FormGroup } from '@angular/forms';
import { NativePageTransitions, NativeTransitionOptions } from '@ionic-native/native-page-transitions';
import { SocketHelperProvider } from "../../providers/sockethelper/sockethelper";

@Component({
  selector: 'page-transfermpesa',
  templateUrl: 'transfermpesa.html',
})
export class TransfermpesaPage{
	private contacts: any[]=[]
	private mpesaformgroup: FormGroup
  private me_other:any[]=[]
  private chosen_me_or_other: any

	constructor(
		private navCtrl: NavController,
		private navParams: NavParams,
		private viewCtrl: ViewController,
		private alertCtrl: AlertController,
    private formBuilder: FormBuilder,
    private socketHelper: SocketHelperProvider,
		private nativePageTransitions: NativePageTransitions){

		//mpesa
    this.mpesaformgroup = this.formBuilder.group({
      m_amount:["",Validators.required],
      m_me_other:["",Validators.required],
      m_contact:[""]
    })

	}

	ionViewDidLoad(){
        //me_other
        this.me_other = [
            {
                option:"Me",
                value:"me"
            },
            {
                option:"Other",
                value:"other"
            }
        ]

		//contact list
		this.contacts = [
			{
				name:"Brian Abuto",
				phonenumber:"0727677068"
			},
			{
				name:"Risper Kemunto",
				phonenumber:"0713318756"
			},
			{
				name:"Charles Mwamba",
				phonenumber:"07214956"
			},
		]
	}

	//handle events
	dismiss(){
		/**Animate*/
	    let options: NativeTransitionOptions = {
	      direction: 'up',
	      duration: 400
	     };
	    this.nativePageTransitions.flip(options);
	    /**End Animation*/
		this.viewCtrl.dismiss()
	}

	onSelectContact(data){
		console.log(typeof data)
		var del = (<HTMLInputElement>document.getElementById("m_contact_del"))
		console.log(del.value)
		del.value = ""
		var holder = (<HTMLInputElement>document.getElementById("m_contact_holder"))

		holder.value = `${data}`
		console.log(holder.value)

  }

  onSelectMeOrOther(data){
    this.chosen_me_or_other = data
  }

	onmpesa(){
		let payAlert = this.alertCtrl.create({
			title:"Confirm",
			message:`You are about to transfer Ksh ${this.mpesaformgroup.value.m_amount} to mobile number ${this.mpesaformgroup.value.m_contact}.`,
			buttons: [
				{
				  text: 'Cancel',
				  handler: () => {
				    console.log('Disagree clicked');
				  }
				},
				{
				  text: 'Agree',
				  handler: () => {
            let user = {
              phoneNumber:254727677,
              memberNnumber:123456789
            }
            let data = {}

            if(this.chosen_me_or_other === 'me'){
              data = {
                ...user,
                meOther:this.mpesaformgroup.value.m_me_other,
                amount:this.mpesaformgroup.value.m_amount
              }
            }else{
              data = {
                ...user,
                meOther:this.mpesaformgroup.value.m_me_other,
                amount:this.mpesaformgroup.value.m_amount,
                contact:this.mpesaformgroup.value.m_contact
              }
            }

            this.socketHelper.mpesaFundsTransfer({
              ...data
            })
				    console.log('Agree clicked');
				  }
				}
			],
			cssClass:'alertCustomCss'
		})

		payAlert.present()
	}

}



