import { Component } from '@angular/core';
import { NavController, NavParams, LoadingController } from 'ionic-angular';
import { HomePage } from '../home/home';

// Providers
import { SocketHelperProvider } from "../../providers/sockethelper/sockethelper";
import { HttpProvider } from "../../providers/http/http";

@Component({
  selector: 'page-signin',
  templateUrl: 'signin.html',
})
export class SigninPage {
  private password:any;
  private phonenumber:any;
  private loading: any;

	constructor(
    private navCtrl: NavController,
    private loadingCtrl: LoadingController, 
    private socketHelper: SocketHelperProvider,
    private http: HttpProvider,
    private navParams: NavParams) {
	}

	ionViewDidLoad() {
    this.showLoader();
      
    //Check if already authenticated
    this.http.checkAuthentication().then((res) => {
      // console.log("Already authorized");
      this.loading.dismiss();
      this.navCtrl.setRoot(HomePage);
    }, (err) => {
      // console.log("Not already authorized");
      this.loading.dismiss();
    });	
	}

	//event handling
	onCancel(){
		this.navCtrl.pop()
	}

	onSignin(){
    let data = {
      passcode:this.password
    }

    this.http.login(data).then((val:any) => {
      console.log(val)
      if(val.success === true){
        this.navCtrl.setRoot(HomePage)
      }
    })



    // this.socketHelper.signIn(data).then((res)=>{
    //   console.log(res)
    // })
		// this.navCtrl.push(HomePage)
  }
  
  showLoader(){
 
      this.loading = this.loadingCtrl.create({
          content: 'Authenticating...'
      });

      this.loading.present();

  } 


}



