import { Component } from '@angular/core';
import { NavController, NavParams, ViewController, AlertController } from 'ionic-angular';
import { Validators, FormBuilder, FormGroup } from '@angular/forms';
import { NativePageTransitions, NativeTransitionOptions } from '@ionic-native/native-page-transitions';
import { SocketHelperProvider } from "../../providers/sockethelper/sockethelper";

@Component({
  selector: 'page-expressloanmodal',
  templateUrl: 'expressloanmodal.html',
})
export class ExpressloanmodalPage {
	private expressformgroup: FormGroup

	constructor(
		private navCtrl: NavController,
		private navParams: NavParams,
		private viewCtrl: ViewController,
		private alertCtrl: AlertController,
    private formBuilder: FormBuilder,
    private socketHelper: SocketHelperProvider,
		private nativePageTransitions: NativePageTransitions){

		//express loan data
		this.expressformgroup = this.formBuilder.group({
			e_amount:["",Validators.required]
		})
	}

	ionViewDidLoad(){}

	//handle events
	dismiss(){
		/**Animate*/
	    let options: NativeTransitionOptions = {
	      direction: 'up',
	      duration: 400
	     };
	    this.nativePageTransitions.flip(options);
	    /**End Animation*/
		this.viewCtrl.dismiss()
	}

	/*On submit express loan function*/
	onSubmitExpress(){
		console.log(this.expressformgroup.value)

		let payAlert = this.alertCtrl.create({
			title:"Confirm",
			message:`You are about to apply for a Ksh ${this.expressformgroup.value.e_amount} express loan.`,
			buttons: [
				{
				  text: 'Cancel',
				  handler: () => {
				    console.log('Disagree clicked');
				  }
				},
				{
				  text: 'Agree',
				  handler: () => {
            this.socketHelper.applyExpressLoan({
              phone_number:254727677068,
              member_number:123456789,
              amount:this.expressformgroup.value.e_amount
            })
				    console.log('Agree clicked');
				  }
				}
			],
			cssClass:'alertCustomCss'
		})

		payAlert.present()
	}

}



