import { Component } from '@angular/core';
import { NavController, ViewController, ModalController } from 'ionic-angular';
import { AlertsPage } from '../alerts/alerts';
import { ChatPage } from '../chat/chat';
import { HomePage } from '../home/home';
import { BalanceenquiryPage } from '../balanceenquiry/balanceenquiry';
import { MinistatementenquiryPage } from '../ministatementenquiry/ministatementenquiry';
import { FullstatementenquiryPage } from '../fullstatementenquiry/fullstatementenquiry';

import { NativePageTransitions, NativeTransitionOptions } from '@ionic-native/native-page-transitions';
import { FabtoggleProvider } from "../../providers/fabtoggle/fabtoggle";

@Component({
  selector: 'page-enquiries',
  templateUrl: 'enquiries.html',
})
export class EnquiriesPage {

	private toggleFab: boolean = false

	constructor(
		public navCtrl: NavController, 
		private modalCtrl: ModalController,
		private viewCtrl: ViewController,
		private fabToggleProvider: FabtoggleProvider,
		private nativePageTransitions: NativePageTransitions){}

	ionViewDidLoad(){}

	//handle events
	onAlert(){
		let notifications = this.modalCtrl.create(AlertsPage)
		notifications.present();
	}

	onChat(){
		let notifications = this.modalCtrl.create(ChatPage)
		notifications.present();
	}	
	onToggleFab(){
		this.toggleFab = !this.toggleFab
		this.fabToggleProvider.toggleFab(this.toggleFab)		
	}	

	onHome(){
		this.navCtrl.push(HomePage)
	}	

	handleBalanceEnquiry(){
		let balEnq = this.modalCtrl.create(BalanceenquiryPage)
		balEnq.present();
	}
	handleMiniStatement(){
		let miniEnq = this.modalCtrl.create(MinistatementenquiryPage)
		miniEnq.present();				
	}
	handleFullStatement(){
		let fullEnq = this.modalCtrl.create(FullstatementenquiryPage)
		fullEnq.present();			
	}
}



