import { Component } from '@angular/core';
import { NavController, NavParams, ViewController, AlertController } from 'ionic-angular';
import { Validators, FormBuilder, FormGroup } from '@angular/forms';
import { NativePageTransitions, NativeTransitionOptions } from '@ionic-native/native-page-transitions';
import { SocketHelperProvider } from "../../providers/sockethelper/sockethelper";

@Component({
  selector: 'page-transferbank',
  templateUrl: 'transferbank.html',
})
export class TransferbankPage{
	private banks: any[]=[]
private branch: any[]=[]
	private bankformgroup: FormGroup

	constructor(
		private navCtrl: NavController,
		private navParams: NavParams,
		private viewCtrl: ViewController,
		private alertCtrl: AlertController,
    private formBuilder: FormBuilder,
    private socketHelper: SocketHelperProvider,
		private nativePageTransitions: NativePageTransitions){

		this.bankformgroup = this.formBuilder.group({
			b_amount:["",Validators.required],
			b_accountnumber:["",Validators.required],
      b_bank:["",Validators.required],
      b_branch:["",Validators.required]
		})
	}

	ionViewDidLoad(){
		//banks list
		this.banks =["K.C.B","CO-OP BANK","I&M BANK","FAULU BANK","EQUITY BANK"]
    this.branch = ["Kisumu Tom Mboya Street","Nairobi Moi Avenue"]
	}

	//handle events
	dismiss(){
		/**Animate*/
	    let options: NativeTransitionOptions = {
	      direction: 'up',
	      duration: 400
	     };
	    this.nativePageTransitions.flip(options);
	    /**End Animation*/
		this.viewCtrl.dismiss()
	}

	onbank(){
		console.log(this.bankformgroup.value)

		let payAlert = this.alertCtrl.create({
			title:"Confirm",
			message:`You are about to transfer Ksh ${this.bankformgroup.value.b_amount} to account number ${this.bankformgroup.value.b_accountnumber} at ${this.bankformgroup.value.b_bank}.`,
			buttons: [
				{
				  text: 'Cancel',
				  handler: () => {
				    console.log('Disagree clicked');
				  }
				},
				{
				  text: 'Agree',
				  handler: () => {
            this.socketHelper.bankFundsTransfer({
              phone_number:254727677068,
              member_number: 123456789,
              bank:this.bankformgroup.value.b_bank,
              branch:this.bankformgroup.value.b_branch,
              account_number:this.bankformgroup.value.b_accountnumber,
              amount:this.bankformgroup.value.b_amount
            })
				    console.log('Agree clicked');
				  }
				}
			],
			cssClass:'alertCustomCss'
		})

		payAlert.present()
	}


}



