import { Component } from '@angular/core';
import { NavController, NavParams, ViewController, AlertController, ToastController, ModalController } from 'ionic-angular';
import { Validators, FormBuilder, FormGroup } from '@angular/forms';
import { NativePageTransitions, NativeTransitionOptions } from '@ionic-native/native-page-transitions';
import { LoancalcPage } from '../loancalc/loancalc';
import { SocketHelperProvider } from "../../providers/sockethelper/sockethelper";

@Component({
  selector: 'page-loanpay',
  templateUrl: 'loanpay.html',
})
export class LoanpayPage {
	private loans:any[]=[]
	private loanpayformgroup: FormGroup

  constructor(
  	public navCtrl: NavController,
  	public navParams: NavParams,
  	public viewCtrl: ViewController,
  	private modalCtrl: ModalController,
  	private alertCtrl:AlertController,
    private formBuilder: FormBuilder,
    private socketHelper: SocketHelperProvider,
  	private nativePageTransitions: NativePageTransitions){

  	this.loanpayformgroup = this.formBuilder.group({
  		lp_amount:["",Validators.required],
  		lp_loan:["",Validators.required],
  		lp_payvia:["",Validators.required]
  	})
  }

	ionViewDidLoad(){
		this.loans = [
			{
				type:"M-Loan",
				amount:"20000",
				balance:"10000"
			},
			{
				type:"Express Loan",
				amount:"100000",
				balance:"50000"
			},
		]
	}

	//handle events
	dismiss(){
		/**Animate*/
	    let options: NativeTransitionOptions = {
	      direction: 'up',
	      duration: 400
	     };
	    this.nativePageTransitions.flip(options);
	    /**End Animation*/
		this.viewCtrl.dismiss()
	}

	onCalc(){
		let loanCalcModal = this.modalCtrl.create(LoancalcPage)
		loanCalcModal.present()
	}

	onSubmit(){
		console.log(this.loanpayformgroup.value)
		let payAlert = this.alertCtrl.create({
			title:"Confirm",
			message:`You are about to reimburse ${this.loanpayformgroup.value.lp_amount} in ${this.loanpayformgroup.value.lp_loan}s.`,
			buttons: [
				{
				  text: 'Cancel',
				  handler: () => {
				    console.log('Disagree clicked');
				  }
				},
				{
				  text: 'Agree',
				  handler: () => {
            this.socketHelper.payLoan({
              phoneNumber:254727677068,
              memberNumber:123456789,
              amount:this.loanpayformgroup.value.lp_amount,
              loan:this.loanpayformgroup.value.lp_loan,
              payVia:this.loanpayformgroup.value.lp_payvia
            })
				    console.log('Agree clicked');
				  }
				}
			],
			cssClass:'alertCustomCss'
		})

		payAlert.present()
	}

}



