import { Component } from '@angular/core';
import { NavController, NavParams, ViewController, AlertController } from 'ionic-angular';
import { Validators, FormBuilder, FormGroup } from '@angular/forms';
import { NativePageTransitions, NativeTransitionOptions } from '@ionic-native/native-page-transitions';
import { SocketHelperProvider } from "../../providers/sockethelper/sockethelper";

@Component({
  selector: 'page-transferkbs',
  templateUrl: 'transferkbs.html',
})
export class TransferkbsPage{
	private kbsaccoformgroup: FormGroup
	private me_other:any[]=[]
  private accounts:any[]=[]
  private chosen_me_or_other: any;

	constructor(
		private navCtrl: NavController,
		private navParams: NavParams,
		private viewCtrl: ViewController,
		private alertCtrl: AlertController,
    private formBuilder: FormBuilder,
    private socketHelper: SocketHelperProvider,
		private nativePageTransitions: NativePageTransitions){

		//kbsacco
		this.kbsaccoformgroup = this.formBuilder.group({
			k_amount:["",Validators.required],
			k_me_other:["",Validators.required],
			k_account:[""],
			k_accountnumber:[""]
		})
	}


	ionViewDidLoad(){
        //me_other
        this.me_other = [
            {
                option:"Me",
                value:"me"
            },
            {
                option:"Other",
                value:"other"
            }
        ]

        //accounts
        this.accounts = [
        	{
        		accountname:"Savings Account",
        		accountnumber:"12345678901"
        	},
        	{
        		accountname:"Biashara Account",
        		accountnumber:"12345678901"
        	},
        ]


	}

	//handle events
	dismiss(){
		/**Animate*/
	    let options: NativeTransitionOptions = {
	      direction: 'up',
	      duration: 400
	     };
	    this.nativePageTransitions.flip(options);
	    /**End Animation*/
		this.viewCtrl.dismiss()
  }

  onSelectMeOrOther(data){
    this.chosen_me_or_other = data
  }

	onkbsacco(){
		console.log(this.kbsaccoformgroup.value)

		let payAlert = this.alertCtrl.create({
			title:"Confirm",
			message:`You are about to transfer Ksh ${this.kbsaccoformgroup.value.k_amount} to account ${this.kbsaccoformgroup.value.k_accountnumber}.`,
			buttons: [
				{
				  text: 'Cancel',
				  handler: () => {
				    console.log('Disagree clicked');
				  }
				},
				{
				  text: 'Agree',
				  handler: () => {
            let user = {
              phoneNumber:254727677,
              memberNumber:123456789
            }
            let data = {}

            if(this.chosen_me_or_other === 'me'){
              data = {
                ...user,
                meOther:this.kbsaccoformgroup.value.k_me_other,
                account:this.kbsaccoformgroup.value.k_account,
                amount:this.kbsaccoformgroup.value.k_amount
              }
            }else{
              data = {
                ...user,
                meOther:this.kbsaccoformgroup.value.k_me_other,
                accountNumber:this.kbsaccoformgroup.value.k_accountnumber,
                amount:this.kbsaccoformgroup.value.k_amount,
              }
            }

            console.log(data)

            this.socketHelper.internalFundsTransfer(data)
				    console.log('Agree clicked');
				  }
				}
			],
			cssClass:'alertCustomCss'
		})

		payAlert.present()
	}

}



