import { Component, ViewChild } from '@angular/core';
import { Nav, NavController, ModalController } from 'ionic-angular';
import { AlertsPage } from '../alerts/alerts';


import { FormPage } from '../form/form';
import { ChatPage } from '../chat/chat';

// import { HomePage } from '../home/home';
import { BuyPage } from '../buy/buy';
import { StartPage } from '../start/start';
import { AccountsPage } from '../accounts/accounts';
import { LoansPage } from '../loans/loans';
import { PricingPage } from '../pricing/pricing';
import { EnquiriesPage } from '../enquiries/enquiries';
import { AboutPage } from '../about/about';
import { InfoPage } from "../info/info";
import { FabtoggleProvider } from "../../providers/fabtoggle/fabtoggle"
import { HttpProvider } from "../../providers/http/http";

@Component({
  selector: 'page-home',
  templateUrl: 'home.html'
})
export class HomePage {
	@ViewChild(Nav) nav: Nav;

	private accountsglimpse:any[]=[]
	private transactionsglimpse:any[]=[]
	private loansglimpse:any[]=[]
	private toggleFab: boolean = false

	constructor(
		public navCtrl: NavController,
		public modalCtrl: ModalController,
		public fabToggleProvider: FabtoggleProvider,
		public http: HttpProvider
		){	
	}

	ionViewDidLoad(){
		this.accountsglimpse = [
			{
				account:"Checking",
				currentbalance:"2000.00"
			},
			{
				account:"Savings",
				currentbalance:"3000.00"
			},
			{
				account:"Visa",
				currentbalance:"1000.00"
			}					
		]

		this.transactionsglimpse = [
			{
				recepient:{
					name:"Dennis Obel",
					accountnumber:"123456789"
				},
				accountfrom:{
					name:"Checking",
					accountnumber:"321654987"
				},
				amount:"1000.00",
				date:"1/2/2018"
			},
			{
				recepient:{
					name:"Shem Ogembo",
					accountnumber:"987654321"
				},
				accountfrom:{
					name:"Savings",
					accountnumber:"987321456"
				},
				amount:"1500.00",
				date:"2/3/2018"
			},			
		]

		this.loansglimpse = [
			{
				amount:"900000",
				type:"Main Loan"
			},
			{
				amount:"700000",
				type:"Main Loan"
			}						
		]
	}

	//handle events
	onAlert(){
		let notifications = this.modalCtrl.create(AlertsPage)
		notifications.present();
	}

	onChat(){
		let notifications = this.modalCtrl.create(ChatPage)
		notifications.present();
	}	

	onAccountsGlimpse(){
		this.navCtrl.push(AccountsPage)
	}

	onLoansGlimpse(){
		this.navCtrl.push(LoansPage)
	}

	// onTransactionsGlimpse(){
	// 	this.navCtrl.push(FormPage)
	// }

	onToggleFab(){
		this.toggleFab = !this.toggleFab
		this.fabToggleProvider.toggleFab(this.toggleFab)
		// this.toggleFab = !this.toggleFab

		// if(this.toggleFab){
		// 	console.log("opened")
		// 	document.getElementById("body").style.display = "none";
		// }else{
		// 	console.log("closed")
		// 	document.getElementById("body").style.display = "block";
		// }
		// console.log("fab toggled:" + this.toggleFab);
		// document.getElementById("body").style.backgroundColor = "red";
		
	}



	/*Alt Landing Page ops */
	onHome(){
	// this.navCtrl.setRoot(HomePage)
	}

	onAccounts(){    
	this.navCtrl.setRoot(AccountsPage)
	}

	onBuy(){
	this.navCtrl.setRoot(BuyPage)
	}

	onLoans(){
	this.navCtrl.setRoot(LoansPage)
	}

	onPricing(){
	console.log("pricing works")
	this.navCtrl.setRoot(PricingPage) 
	}

	onEnquiries(){
	console.log("enquiries works")
	this.navCtrl.setRoot(EnquiriesPage) 
	}

	onAbout(){
	this.navCtrl.setRoot(AboutPage) 
	}	

	onInfo(){
		let info = this.modalCtrl.create(InfoPage)
		info.present();
	}

	onLogOut(){
		this.http.logout().then(()=>{
			this.navCtrl.setRoot(StartPage)
		})		
	}

}



