import { Component, ViewChild } from '@angular/core';
import { NavController, NavParams, ModalController } from 'ionic-angular';
import { AlertsPage } from '../alerts/alerts';
import { ChatPage } from '../chat/chat';
import { HomePage } from '../home/home';
import { LoanapplyPage } from '../loanapply/loanapply';
import { LoanpayPage } from '../loanpay/loanpay';
import { GuaranteesPage } from '../guarantees/guarantees';
import { Chart } from 'chart.js';
import { FabtoggleProvider } from "../../providers/fabtoggle/fabtoggle";
import { SocketHelperProvider } from "../../providers/sockethelper/sockethelper";

@Component({
  selector: 'page-loans',
  templateUrl: 'loans.html',
})
export class LoansPage {
	@ViewChild('barCanvas') barCanvas;

	private qualifyfor:any[]=[]
	private pendingloans:any[]=[]
	private pending:any[]=[]
	private barChart: any;
	private toggleFab: boolean = false
  private hiddenbtn: any
  private loanBalances: any[]=[]

	constructor(
		public navCtrl: NavController,
		public navParams: NavParams,
    public modalCtrl: ModalController,
    public socketHelper: SocketHelperProvider,
		public fabToggleProvider: FabtoggleProvider
	){}

	ionViewDidEnter(){
		// this.hiddenbtn = document.getElementById("otherloanops")
		// this.hiddenbtn.style.display = "none";
	}
	ionViewDidLoad(){
    // load loan balances
    this.socketHelper
    .getLoanBal()
    .then((res)=>{

    })

		//loans qualified for
		this.qualifyfor = [
			{
				amount:"50000",
				type:"M-Loan"
			},
			{
				amount:"100000",
				type:"Micro Loan"
			},
			{
				amount:"1000000",
				type:"Development Loan"
			},
			{
				amount:"700000",
				type:"Education Loan"
			},
			{
				amount:"400000",
				type:"Express Loan"
			},
			{
				amount:"30000",
				type:"FOSA Loan"
			}
		]

		//pending loans
		this.pending = [
			{
				type:"FOSA",
				datetaken:"01/01/2018",
				datedue:"01/01/2019",
				amounttaken:"120000",
				amountpaid:"40000",
				balance:"80000",
				thismonthpaidstatus:"paid"
			},
			{
				type:"M-Loan",
				datetaken:"01/04/2018",
				datedue:"01/09/2018",
				amounttaken:"20000",
				amountpaid:"5000",
				balance:"15000",
				thismonthpaidstatus:"paid"
			}
		]

		//pending graph

		interface LooseObject {
			[key: string]: any
		}

		let graph: LooseObject = {}
		let lbls: any[]=[]
		let paid: any[]=[]
		let bal: any[]=[]

		for(var i = 0; i < this.pending.length; i++){
			lbls.push(this.pending[i].type)
			paid.push(this.pending[i].amountpaid)
			bal.push(this.pending[i].balance)
			graph.type="bar",
			graph.data={
				labels : lbls,
				datasets:[
					{
						type:"bar",
						label:"Paid",
						data:paid,
	                    backgroundColor: [
	                        'rgba(255, 99, 132, 0.2)',
	                        'rgba(54, 162, 235, 0.2)',
	                        'rgba(255, 206, 86, 0.2)',
	                        'rgba(75, 192, 192, 0.2)',
	                        'rgba(153, 102, 255, 0.2)',
	                        'rgba(255, 159, 64, 0.2)'
	                    ],
	                    borderColor: [
	                        'rgba(255,99,132,1)',
	                        'rgba(54, 162, 235, 1)',
	                        'rgba(255, 206, 86, 1)',
	                        'rgba(75, 192, 192, 1)',
	                        'rgba(153, 102, 255, 1)',
	                        'rgba(255, 159, 64, 1)'
	                    ],
	                    borderWidth: 1
					},

					{
						type:"bar",
						label:"Balance",
						data:bal,
	                    backgroundColor: [
	                        'rgba(255,99,132,1)',
	                        'rgba(54, 162, 235, 1)',
	                        'rgba(255, 206, 86, 1)',
	                        'rgba(75, 192, 192, 1)',
	                        'rgba(153, 102, 255, 1)',
	                        'rgba(255, 159, 64, 1)'
	                    ],
	                    borderColor: [
	                        'rgba(255, 99, 132, 0.2)',
	                        'rgba(54, 162, 235, 0.2)',
	                        'rgba(255, 206, 86, 0.2)',
	                        'rgba(75, 192, 192, 0.2)',
	                        'rgba(153, 102, 255, 0.2)',
	                        'rgba(255, 159, 64, 0.2)'
	                    ],
	                    borderWidth: 1
					}
				]
			},
			graph.options= {
		        legend: {
		        	fontColor: "black",
		            labels: {
		                fontColor: "black",
		                fontSize: 12
		            },
		            label: {
		                fontColor: "black",
		                fontSize: 12
		            }
		        },
				scales: {
				  xAxes: [
				  	{
				  		stacked: true ,
						gridLines: {
						    display: false,
						},
						ticks: {
						  	fontColor: "black",
		                	fontSize: 12
						},
				  	}
				  ],
				  yAxes: [
				  	{
				  		stacked: true ,
						gridLines: {
						    display: false,
						},
						ticks: {
						  	fontColor: "black",
		                	fontSize: 12
						},
				  	}
				  ],
				}
			}
		}

		console.log(graph)
		this.barChart = new Chart(this.barCanvas.nativeElement, graph)
    }



	//handle events
	onAlert(){
		let notifications = this.modalCtrl.create(AlertsPage)
		notifications.present();
	}

	onChat(){
		let notifications = this.modalCtrl.create(ChatPage)
		notifications.present();
	}

	onApply(){
		let notifications = this.modalCtrl.create(LoanapplyPage)
		notifications.present();
	}

	onPay(){
		let notifications = this.modalCtrl.create(LoanpayPage)
		notifications.present();
	}

	onGuarantees(){
		let notifications = this.modalCtrl.create(GuaranteesPage)
		notifications.present();
	}

	onToggleFab(){
		this.toggleFab = !this.toggleFab
		this.fabToggleProvider.toggleFab(this.toggleFab)



		// if(this.toggleFab == true){
		// 	console.log("block")
		// 	this.hiddenbtn.style.display = "block";
		// }else{
		// 	console.log("none")
		// 	this.hiddenbtn.style.display = "none";
		// }

		// let newbtns = `
		// 	<button
		// 	(click)="onApply()"
	 //        class="otheropsbtn"
		// 	style="color: #ede7f6"
		// 	color="lightprimarycolor"
		// 	ion-button
		// 	outline
		// 	block>APPLY
		// 	</button>

		// 	<button
		// 	(click)="onPay()"
	 //        class="otheropsbtn"
		// 	style="color: #ede7f6"
		// 	color="lightprimarycolor"
		// 	ion-button
		// 	outline
		// 	block>PAY
		// 	</button>

		// 	<button
		// 	(click)="onGuarantees()"
	 //        class="otheropsbtn"
		// 	style="color: #ede7f6"
		// 	color="lightprimarycolor"
		// 	ion-button
		// 	outline
		// 	block>GUARANTEES
		// 	</button>
		// `

		// this.hiddenbtn.innerHTML = newbtns
	}


	onHome(){
		this.navCtrl.push(HomePage)
	}
}



