import { Component, ViewChild } from '@angular/core';
import { Nav, Platform, ModalController, AlertController } from 'ionic-angular';
import { StatusBar } from '@ionic-native/status-bar';
import { SplashScreen } from '@ionic-native/splash-screen';

import { HomePage } from '../pages/home/home';
import { BuyPage } from '../pages/buy/buy';
import { StartPage } from '../pages/start/start';
import { AccountsPage } from '../pages/accounts/accounts';
import { LoansPage } from '../pages/loans/loans';
import { PricingPage } from '../pages/pricing/pricing';
import { EnquiriesPage } from '../pages/enquiries/enquiries';
import { AboutPage } from '../pages/about/about';
// import { InfoPage } from '../pages/info/info';

// Providers
import { HttpProvider } from "../providers/http/http";

@Component({
  templateUrl: 'app.html'
})
export class MyApp {
  @ViewChild(Nav) nav: Nav;

  // rootPage: any = HomePage;
  rootPage: any = StartPage;

  pages: Array<{title: string, icon: string, component: any}>;

  constructor(
    public platform: Platform, 
    public statusBar: StatusBar, 
    public splashScreen: SplashScreen, 
    public modalCtrl: ModalController,
    public alertCtrl: AlertController,
    private http: HttpProvider) {
    this.initializeApp();

    // used for an example of ngFor and navigation
    this.pages = [
      { title: 'Utilities & Bills',icon:'md-cart', component: BuyPage },
      { title: 'Funds Transfer', icon:'md-cash', component: AccountsPage },
      { title: 'Loans', icon:'md-card', component: LoansPage },
      { title: 'Pricing', icon:'md-pricetag', component: PricingPage },
      { title: 'About Us', icon:'md-information-circle', component: AboutPage }
    ];

  }

  initializeApp() {
    this.platform.ready().then(() => {
      // Okay, so the platform is ready and our plugins are available.
      // Here you can do any higher level native things you might need.
      this.statusBar.styleDefault();
      this.splashScreen.hide();
    });
  }

  //handle events
  // onAlert(){
  //   let notifications = this.modalCtrl.create(AlertsPage)
  //   notifications.present();
  // }

  openPage(page) {
    // Reset the content nav to have just this page
    // we wouldn't want the back button to show in this scenario
    this.nav.setRoot(page.component);
  }

  onHome(){
    this.nav.setRoot(HomePage)
  }

  onAccounts(){    
    this.nav.setRoot(AccountsPage)
  }

  onBuy(){
    this.nav.setRoot(BuyPage)
  }

  onLoans(){
    this.nav.setRoot(LoansPage)
  }

  onPricing(){
    console.log("pricing works")
    this.nav.setRoot(PricingPage) 
  }

  onEnquiries(){
    console.log("enquiries works")
    this.nav.setRoot(EnquiriesPage) 
  }

  onAbout(){
    this.nav.setRoot(AboutPage) 
  }

  onReport(){
    let prompt = this.alertCtrl.create({
      title:"STATEMENT REQUEST",
      message:"Your statement will be submitted to your email.",      
      buttons:[
        {
          text:"Send Statement",
          handler: data =>{
            console.log(data);
          }
        }
      ],
      cssClass:'alertCustomCss' 
    })
    prompt.present()
  }

  onLogOut(){
    this.http.logout().then(()=>{
      this.nav.setRoot(StartPage)
    })
		// this.navCtrl.setRoot(LoginPage)
    
  }
}



