import { Component, ViewChild, ElementRef } from '@angular/core';
import { NavController, NavParams, ModalController } from 'ionic-angular';
import { AlertsPage } from '../alerts/alerts';
import { ChatPage } from '../chat/chat';
import { HomePage } from '../home/home';
import { FabtoggleProvider } from "../../providers/fabtoggle/fabtoggle";


import { Http } from "@angular/http"
import 'rxjs/add/operator/map';

declare var google;

@Component({
  selector: 'page-about',
  templateUrl: 'about.html',
})
export class AboutPage {
	@ViewChild('mapcontainer') mapElement: ElementRef;
	map: any;	

	private toggleFab: boolean = false



	constructor(
		private navCtrl: NavController, 
		private modalCtrl: ModalController,
		private navParams: NavParams,
		private fabToggleProvider: FabtoggleProvider,
		private http: Http){}

	ionViewWillEnter(){	
		this.initializeMap();
		this.getMarkers();
	}	

	//handle events
	onAlert(){
		let notifications = this.modalCtrl.create(AlertsPage)
		notifications.present();
	}

	onChat(){
		let notifications = this.modalCtrl.create(ChatPage)
		notifications.present();
	}	

	onToggleFab(){
		this.toggleFab = !this.toggleFab
		this.fabToggleProvider.toggleFab(this.toggleFab)		
	}	

	onCall(){
		console.log("calling kbsacco")
	}

	//Handle Map

	initializeMap() {

	    let latLng = new google.maps.LatLng(-1.2938871, 36.807509)
	    let mapOptions = {
	    	center: latLng,
	    	disableDefault: true,
	    	zoom: 12,
	    	mapTypeId: google.maps.MapTypeId.ROADMAP
	    }

	    this.map = new google.maps.Map(this.mapElement.nativeElement, mapOptions)
	}

	getMarkers(){
		this.http.get("assets/data/location.json")
		.map((res)=>res.json())
		.subscribe(data=>{
			this.addMarkersToMap(data)
			console.log(data)
		})
	}

	addMarkersToMap(location){
		for(let loc of location){
			var position = new google.maps.LatLng(loc.latitude,loc.longitude)
			var locMarker = new google.maps.Marker({position:position,title:loc.title})
			locMarker.setMap(this.map)
		}
	}	

	onHome(){
		this.navCtrl.push(HomePage)
	}	

}



