import { Component } from '@angular/core';
import { NavController, NavParams, ModalController } from 'ionic-angular';
import { AlertsPage } from '../alerts/alerts';
import { ChatPage } from '../chat/chat';
import { HomePage } from '../home/home';
import { SendmoneypricingPage } from "../sendmoneypricing/sendmoneypricing";
import { PaybillspricingPage } from "../paybillspricing/paybillspricing";
import { BuyairtimepricingPage } from "../buyairtimepricing/buyairtimepricing";
import { AccountpricingPage } from "../accountpricing/accountpricing";
import { LoanpricingPage } from "../loanpricing/loanpricing";
import { FabtoggleProvider } from "../../providers/fabtoggle/fabtoggle";


@Component({
  selector: 'page-pricing',
  templateUrl: 'pricing.html',
})
export class PricingPage {
	private toggleFab: boolean = false
	private pricecatalogue: any[]=[]

	constructor(
		private navCtrl: NavController, 
		private modalCtrl: ModalController,
		private navParams: NavParams,
		private fabToggleProvider: FabtoggleProvider){}

	ionViewDidLoad(){}

	//handle events
	onAlert(){
		let notifications = this.modalCtrl.create(AlertsPage)
		notifications.present();
	}

	onChat(){
		let notifications = this.modalCtrl.create(ChatPage)
		notifications.present();
	}	
	onToggleFab(){
		this.toggleFab = !this.toggleFab
		this.fabToggleProvider.toggleFab(this.toggleFab)		
	}	

	onAccount(){
		let notifications = this.modalCtrl.create(AccountpricingPage)
		notifications.present();		
	}

	onSendMoney(){
		let notifications = this.modalCtrl.create(SendmoneypricingPage)
		notifications.present();
	}

	onPayBills(){
		let notifications = this.modalCtrl.create(PaybillspricingPage)
		notifications.present();
	}

	onBuyAirtime(){
		let notifications = this.modalCtrl.create(BuyairtimepricingPage)
		notifications.present();
	}

	onLoans(){
		let loanpricing = this.modalCtrl.create(LoanpricingPage)
		loanpricing.present()
	}

	onHome(){
		this.navCtrl.push(HomePage)
	}

}



